:root {
  --main-bg-color: #00acdc;
}

body {
  background: #dacece;
  margin: 0;
}

.all {
  width: 100%;
  height: 100vh;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.nav {
  align-items: center;
  height: 100%;
  margin-left: 0;
  display: flex;
}

.header__logo {
  align-items: center;
  display: flex;
}

.logo-img {
  width: 40px;
  height: 40px;
  display: inline-block;
}

.nav-list {
  padding: 0;
  list-style-type: none;
  display: flex;
}

a.is-active.nav-link {
  margin: 0;
}

.nav-link {
  white-space: nowrap;
  color: #000;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.06px;
  text-decoration: none;
  display: flex;
}

.nav-list:first-child {
  margin-left: 0;
}

.is-active {
  color: var(--main-bg-color);
  position: relative;
}

.is-active, .nav-link:hover {
  color: var(--main-bg-color);
}

.is-active:after {
  content: "";
  background-color: var(--main-bg-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

